.container{
    display: flex;
    flex-direction: column;
    max-height: 100vh;
    margin-top: 30px;
    margin-bottom: 20px;
    background-color: #5e35b1;
    border: none;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
    color: white;
    align-items: center;
}
.container__header{
    font-size: 3rem;
    margin-top: 20px;
    font-weight: bold;
}
hr {
    width: 95%;
    margin: 0 auto;
    margin-top: 4px;
    border: none;
    height: 2px;
    background-color: #d1c4e9;
}
.tasks_state{
    display: flex;
    align-items: center;
    margin-top: 20px;
    margin-bottom: 20px;
}
.tasks_state_item{
    background-color: white;
    color: rgb(151, 151, 151);
    padding: 12px;
    border: 1px solid rgb(216, 215, 215);
    cursor: pointer;
}

.tasks_state_item:hover{
    background-color: #c9c8cb;
    color: black;
    border: 1px solid white;
}
.Add_task{
    width: 100%;
    margin-top: 10px;
    padding-top: 5px;
    padding-bottom: 20px;
}
.Add_task_button{
    background-color: #ca3e78;
    color: white;
    padding: 12px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1.2rem;
    margin-right: 20px;
    transition: background-color 0.3s ease;
}
.Add_task_button:hover{
    background-color: #cb7594;
   
}
.Add_task_input{
    width: 70%;
    padding: 12px;
    border: 1px solid rgb(216, 215, 215);
    border-radius: 4px;
    font-size: 1.2rem;
    margin-right: 20px;
    outline: none;
}
.task_list{
   
    height: auto;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgb(0, 157, 255);
    margin-bottom: 20px;
    margin-top: 10px;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease, padding 0.3s ease;
    scroll-behavior: smooth;
    
}
.task_list:hover{
    background-color: #03c8ff;
    padding-top:35px ;
    padding-bottom: 35px;
    color: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
}
.tasks_list{
    width: 90%;
    max-height: 55%;
    overflow: auto;
    padding-right: 20px;
}

    ::-webkit-scrollbar {
        width: 10px; /* Width of the scrollbar */

      }
      
      ::-webkit-scrollbar-track {
        background: #03c8ff; /* Light background for track */
        border-radius: 10px;
        margin: 10px 0; /* Adds space (gap) at top and bottom */
      }
      
      ::-webkit-scrollbar-thumb {
        background: #03c8ff; /* Color of the scroll thumb */
        border-radius: 10px;
      }
      
      ::-webkit-scrollbar-thumb:hover {
        background: #1c8dad; /* Darker on hover */
      }
      
      /* Optional: Smooth scrolling */
      html {
        scroll-behavior: smooth;
      }

.tasks_list_buttons button{
    background-color: aliceblue;
    padding: 0px;
    border-radius: 50%;
    cursor: pointer;
    margin-left: 20px;
    width: 50px;
    height: 50px;
    transition: background-color 0.3s ease;
    background-color: white;
}
.tasks_list_buttons button:hover{
    background-color: #c9c8cb;
}
#delete_btn{
    border: 3px solid #f02f2ff9;
    transition: background-color 0.3s ease;

}
#delete_btn:hover{
    background-color: #f02f2ff9;
    border: 3px solid white;
    color: white;
}
#edit_btn{
    border: 3px solid gold;
    color: gold;
    transition: background-color 0.3s ease;
}
#edit_btn:hover{
    background-color: gold;
    border: 3px solid white;
    color: white;
}
#check_btn{
    border: 3px solid green;
    color: green;
    transition: background-color 0.3s ease;

}
#check_btn:hover{
    background-color: green;
    border: 3px solid white;
    color: white;
}
.tasks_list_title{
    font-weight: bold;
    font-size: 30px;
}
.tasks_list_detail{
    font-size: 20px;
    margin-top: 10px;
    color: rgb(130, 130, 130);
}
.tasks_state_item.active {
  color: red;
  font-weight: bold;
}